body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.g-relic {
  margin: 20px;
  position:relative;

}

.g-relic-image{
  border:solid 20px  white;
  border-bottom:solid 100px  white;
  width: auto;
  height: 480px;
  box-shadow: 0px 0px 3px 1.5px #ccc;
}

.g-relic-info{
  position: absolute;
  bottom:60px;
  left:20px;
  height: 30px;
}

.g-relic-title{
  font-size: 20px;
  margin: 0;
}

.g-relic-location{
  font-weight: lighter;
  margin:0;
}

.g-relic-year{
  font-weight: lighter;
  font-size: 15px;
  color: #7abaff;
  margin:0;
}

.success-msg{
    color: #57A773;
    padding: 10px 15px;
}

.err-msg{
    color: #EE6352;
    padding: 10px 15px;
}

.footer-zitat{
  text-align: center;
  margin:0;
}

.footer-year{
  text-align: right;
}

::-webkit-scrollbar {
    width: 40px;
    background: white;

}
/* Track */
::-webkit-scrollbar-track {
    background: linear-gradient(90deg,#e2e6ea,#e2e6ea 1px, white, white 0px);


}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(122, 186, 255,0.7);

    border-radius: 40px;
    box-shadow:0px 3px 5px 1px #c6c8ca;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {

}



@media only screen and (min-width: 375px) {

  ::-webkit-scrollbar {
    display: none;
  }

  .g-relic-image{
    width: 320px;
    height: auto;
  }
}

@media only screen and (min-width: 640px) {
  .g-relic-image{
    height: auto;
    width:400px;
  }
}

@media only screen and (min-width: 1024px) {

  ::-webkit-scrollbar {
    display: block;
  }

  .g-relic-image{
    height: auto;
    width:600px;
  }

  .g-relic {
    position:relative;
  }

  .g-relic-info{
    position: absolute;
    top:10px;
    left:20px;
    margin-top:0;
    height: 30px;
  }

  .g-relic-location{
    margin:0;
  }

  .g-relic-image{
    border:solid 20px  white;
    border-top:solid 100px  white;
    box-shadow: 0px 0px 3px 1.5px #ccc;
  }
}



